ont-family: monospace, monospace;\n\n    /* 1 */\n    font-size: 1em;\n\n    /* 2 */\n  }\n\n  /**\n * Add the correct font style in Android 4.3-.\n */\n  dfn {\n    font-style: italic;\n  }\n\n  /**\n * Add the correct background and color in IE 9-.\n */\n  mark {\n    background-color: #FFFF00;\n    color: #000000;\n  }\n\n  /**\n * Add the correct font size in all browsers.\n */\n  small {\n    font-size: 80%;\n  }\n\n  /**\n * Prevent `sub` and `sup` elements from affecting the line height in\n * all browsers.\n */\n  sub,\n  sup {\n    font-size: 75%;\n    line-height: 0;\n    position: relative;\n    vertical-align: baseline;\n  }\n\n  sub {\n    bottom: -.25em;\n  }\n\n  sup {\n    top: -.5em;\n  }\n\n  /* Embedded content\n   ========================================================================== */\n\n  /**\n * Add the correct display in IE 9-.\n */\n  audio,\n  video {\n    display: inline-block;\n  }\n\n  /**\n * Add the correct display in iOS 4-7.\n */\n  audio:not([controls]) {\n    display: none;\n    height: 0;\n  }\n\n  /**\n * Remove the border on images inside links in IE 10-.\n */\n  img {\n    border-style: none;\n  }\n\n  /**\n * Hide the overflow in IE.\n */\n  svg:not(:root) {\n    overflow: hidden;\n  }\n\n  /* Forms\n   ========================================================================== */\n\n  /**\n * 1. Change the font styles in all browsers (opinionated).\n * 2. Remove the margin in Firefox and Safari.\n */\n  button,\n  input,\n  optgroup,\n  select,\n  textarea {\n    font-family: sans-serif;\n\n    /* 1 */\n    font-size: 100%;\n\n    /* 1 */\n    line-height: 1.15;\n\n    /* 1 */\n    margin: 0;\n\n    /* 2 */\n  }\n\n  /**\n * Show the overflow in IE.\n * 1. Show the overflow in Edge.\n */\n  button,\n  input {\n    /* 1 */\n    overflow: visible;\n  }\n\n  /**\n * Remove the inheritance of text transform in Edge, Firefox, and IE.\n * 1. Remove the inheritance of text transform in Firefox.\n */\n  button,\n  select {\n    /* 1 */\n    text-transform: none;\n  }\n\n  /**\n * 1. Prevent a WebKit bug where (2) destroys native `audio` and `video`\n *    controls in Android 4.\n * 2. Correct the inability to style clickable types in iOS and Safari.\n */\n  button,\n  html [type=\"button\"],\n  [type=\"reset\"],\n  [type=\"submit\"] {\n    appearance: button;\n\n    /* 2 */\n  }\n\n  /**\n * Remove the inner border and padding in Firefox.\n */\n  button::-moz-focus-inner,\n  [type=\"button\"]::-moz-focus-inner,\n  [type=\"reset\"]::-moz-focus-inner,\n  [type=\"submit\"]::-moz-focus-inner {\n    border-style: none;\n    padding: 0;\n  }\n\n  /**\n * Restore the focus styles unset by the previous rule.\n */\n  button:-moz-focusring,\n  [type=\"button\"]:-moz-focusring,\n  [type=\"reset\"]:-moz-focusring,\n  [type=\"submit\"]:-moz-focusring {\n    outline: 1px dotted ButtonText;\n  }\n\n  /**\n * Correct the padding in Firefox.\n */\n  fieldset {\n    padding: .35em .75em .625em;\n  }\n\n  /**\n * 1. Correct the text wrapping in Edge and IE.\n * 2. Correct the color inheritance from `fieldset` elements in IE.\n * 3. Remove the padding so developers are not caught out when they zero out\n *    `fieldset` elements in all browsers.\n * 4. Try to make it appear completely in the fieldset\n */\n  legend {\n    box-sizing: border-box;\n    box-sizing: border-box;\n\n    /* 1 */\n    color: inherit;\n\n    /* 2 */\n    display: table;\n\n    /* 4 */\n    float: left;\n\n    /* 1 */\n    max-width: 100%;\n\n    /* 1 */\n    padding: 0;\n\n    /* 3 */\n    white-space: normal;\n\n    + * {\n      clear: both;\n    }\n  }\n\n  /**\n * 1. Add the correct display in IE 9-.\n * 2. Add the correct vertical alignment in Chrome, Firefox, and Opera.\n */\n  progress {\n    display: inline-block;\n\n    /* 1 */\n    vertical-align: baseline;\n\n    /* 2 */\n  }\n\n  /**\n * Remove the default vertical scrollbar in IE.\n */\n  textarea {\n    overflow: auto;\n  }\n\n  /**\n * 1. Add the correct box sizing in IE 10-.\n * 2. Remove the padding in IE 10-.\n */\n  [type=\"checkbox\"],\n  [type=\"radio\"] {\n    box-sizing: border-box;\n    box-sizing: border-box;\n\n    /* 1 */\n    padding: 0;\n\n    /* 2 */\n  }\n\n  /**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n  [type=\"number\"]::-webkit-inner-spin-button,\n  [type=\"number\"]::-webkit-outer-spin-button {\n    height: auto;\n  }\n\n  /**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n  [type=\"search\"] {\n    appearance: textfield;\n\n    /* 1 */\n    outline-offset: -2px;\n\n    /* 2 */\n  }\n\n  /**\n * Remove the inner padding and cancel buttons in Chrome and Safari on macOS.\n */\n  [type=\"search\"]::-webkit-search-cancel-button,\n  [type=\"search\"]::-webkit-search-decoration {\n    appearance: none;\n  }\n\n  /**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n  ::-webkit-file-upload-button {\n    appearance: button;\n\n    /* 1 */\n    font: inherit;\n\n    /* 2 */\n  }\n\n  /* Interactive\n   ========================================================================== */\n\n  /*\n * Add the correct display in IE 9-.\n * 1. Add the correct display in Edge, IE, and Firefox.\n */\n  details,\n  menu {\n    display: block;\n  }\n\n  /*\n * Add the correct display in all browsers.\n */\n  summary {\n    display: list-item;\n  }\n\n  /* Scripting\n   ========================================================================== */\n\n  /**\n * Add the correct display in IE 9-.\n */\n  canvas {\n    display: inline-block;\n  }\n\n  /**\n * Add the correct display in IE.\n */\n  template {\n    display: none;\n  }\n\n  /* Hidden\n   ========================================================================== */\n\n  /**\n * Add the correct display in IE 10-.\n */\n  [hidden] {\n    display: none;\n  }\n}\n","// TODO understand why `main` export doesn't work\n\n@use \"@aztlan/mixins/src\" as mixins;\n\n// TODO Cleanup and mixins\n$sm-md:801px;\n$md-lg:1400px;\n\n\n/* The following variables can be configured when using the library */\n\n/* Custom property and class names */\n$default-columns-property:\"columns\" !default;\n$default-column-width-property:\"column-width\" !default;\n$default-gap-property:\"gap\" !default;\n$default-start-class:\"start\" !default;\n$default-span-class:\"span\" !default;\n$default-class-prefix:\"\" !default;\n$md-class-prefix:\"#{$default-class-prefix}md-\" !default;\n\n/* Number of columns */\n$md-columns:14 !default;\n$default-columns:8 !default;\n\n/* Grid gap */\n$default-gap:1rem !default;\n\n/* Library definitions start here */\n\n$default-column-width:\n  calc((100vw - (#{$default-columns} + 1) * #{$default-gap}) / #{$default-columns});\n$md-column-width:\n  calc((100vw - (#{$md-columns} + 1) * #{$default-gap}) / #{$md-columns});\n\n$manual-column-width:\n  calc(\n    var(--#{$default-column-width-property})\n    * var(--#{$default-columns-property})\n    + var(--#{$default-gap-property})\n    * (var(--#{$default-columns-property})\n    - 1\n  )\n);\n\n@include mixins.media($from:$sm-md) {\n  :root {\n    --#{$default-columns-property}: #{$md-columns};\n    --#{$default-column-width-property}: #{$md-column-width};\n  }\n}\n\n:root {\n  --#{$default-columns-property}: #{$default-columns};\n  --#{$default-column-width-property}: #{$default-column-width};\n  --#{$default-gap-property}: #{$default-gap};\n}\n\n.grid {\n  display: grid;\n  gap: var(--#{$default-gap-property});\n  grid-template-columns: repeat(var(--#{$default-columns-property}), 1fr);\n}\n\n.container {\n  grid-column: 1/-1;\n}\n\n.section {\n  &.padded {\n    padding: 0 var(--#{$default-gap-property});\n  }\n}\n\n.dense {\n  grid-auto-flow: row dense;\n}\n\n.manual {\n  min-width: $manual-column-width;\n  width: $manual-column-width;\n}\n\n.manual-mobile-only {\n  @include mixins.media($from:$sm-md) {\n    min-w